import React, { Component } from 'react';
import {connect} from 'react-redux'
import Button from '../../../components/UI/Button/Button';
import Spinner from '../../../components/UI/Spinner/Spinner';
import classes from './ContactData.css';
import axios from '../../../axios-orders';
import Input from '../../../components/UI/Input/Input'
class ContactData extends Component {
    state = {
      loading: false,
      formisValid:false,
      orderForm:{
        name: {
            elementtype:'input',
            elementconfig:{
                placeholder:'YourName',
                type:'text'
            },
            value:'',
            validation:{
                required:true
            },
            valid:false,
            touched:false
        },
        street: {
            elementtype:'input',
            elementconfig:{
                placeholder:'YourStreetName',
                type:'text'
            },
            value:'',
            validation:{
                required:true
            },
            valid:false,
            touched:false
        },
        zipCode:{
            elementtype:'input',
            elementconfig:{
                placeholder:'YourCode',
                type:'text'
            },
            value:'',
            validation:{
                required:true,
                maxlength:5,
                minlength:5
            },
            valid:false,
            touched:false
        },
        country:{
            elementtype:'input',
            elementconfig:{
                placeholder:'YourContry',
                type:'text'
            },
            value:'',
            validation:{
                required:true
            },
            valid:false,
            touched:false
        },
        email: {
            elementtype:'input',
            elementconfig:{
                placeholder:'YourE-Mail',
                type:'e-mail'
            },
            value:'',
            validation:{
                required:true
            },
            valid:false,
            touched:false
        },
        deliveryMethod:{
        elementtype:'select',
        elementconfig:{
            options:[
                {value:'fastest', displayValue:'Fastest'},
                {value:'cheapest', displayValue:'Cheapest'}
            ]
        },
        value:'fastest',
        validation:{
            required:true
        },
        valid:true
        }
    }
}
    checkValidity=(value,rules)=>{
        let isValid = true
        if(rules.required && value == '' && isValid )
        {
            isValid=false
        }
        if(value.length > rules.maxlength && isValid )
        {
            isValid = false
        }
        if(value.length < rules.minlength && isValid )
        {
            isValid = false
        }
        if (rules.isEmail) {
            const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            isValid = pattern.test(value) && isValid
        }

        if (rules.isNumeric) {
            const pattern = /^\d+$/;
            isValid = pattern.test(value) && isValid  
        }
        return isValid
    }
    inputChangeHandler=(event,inputID)=>{
        
        let UpdatedOrderForm = {
            ...this.state.orderForm
        }
        let UpdatedOrderFormEl = {
            ...UpdatedOrderForm[inputID]
        }
        UpdatedOrderFormEl.touched=true
        UpdatedOrderFormEl.value=event.target.value
        UpdatedOrderFormEl.valid=this.checkValidity(UpdatedOrderFormEl.value,UpdatedOrderFormEl.validation)
        UpdatedOrderForm[inputID]=UpdatedOrderFormEl
        let formisValid = true
        for(let i in UpdatedOrderForm)
        {
            formisValid = UpdatedOrderForm[i].valid && formisValid
        }
        this.setState({orderForm:UpdatedOrderForm,formisValid:formisValid})
       
    }
    orderHandler=(event)=>{
        event.preventDefault()
          this.setState( { loading: true } );
        let formdata ={}
        for(let key in this.state.orderForm)
        {formdata[key] = this.state.orderForm[key].value}
        const order = {
            ingredients: this.props.ings,
            price: this.props.totalPrice,
            formdata: formdata
        }
            console.log('order',order)
        axios.post( '/orders.json', order )
            .then( response => {
                this.setState({ loading: false},
                this.console.log(response));
            } )
            .catch( error => {
               this.setState({ loading: false}),
               console.log(error)
            } );
       
    }
    render () {
            
            const formElementArray=[]
           for(let key in this.state.orderForm) 
           {formElementArray.push({
                id:key,
                config:this.state.orderForm[key]
           })}
           let OutputForm = formElementArray.map(formElement=>{
            return(<Input 
                changed={(event,inputID)=>this.inputChangeHandler(event,formElement.id)}
                touched={formElement.config.touched}
                key={formElement.id}
                elementconfig={formElement.config.elementconfig}
                elementtype={formElement.config.elementtype}
                value={formElement.config.value}
                invalid={!formElement.config.valid}
                ></Input>)
        })
        let form = (
            <form onSubmit={this.orderHandler}>
                {OutputForm}
                <Button btnType="Success" disabled={!this.state.formisValid}>ORDER</Button>
            </form>
        );
        if ( this.state.loading ) {
            form = <Spinner />;
        }
        return (
            <div className={classes.ContactData}>
                <h4>Enter your Contact Data</h4>
                {form}
            </div>
        );
    }
}
const mapStateToProps = state =>({
    ings:state.ingredients,
    totalPrice:state.totalPrice
})

export default connect(mapStateToProps)(ContactData);


