import React from 'react'
import classes from './Input.css'
const Input =(props)=>{
    let inputelement = null;
    let inputelementclass = []
    inputelementclass.push(classes.Inputelement)
    if(props.invalid && props.touched)
    {
        inputelementclass.push(classes.Invalid)
    }
        switch(props.elementtype)
        {
            case('input'):
            inputelement =  <input onChange={props.changed} className={inputelementclass.join(' ')}{...props.elementconfig}/>
            break;
            case('textarea'):
            inputelement =  <textarea onChange={props.changed} className={classes.Inputelement}{...props.elementconfig}/>
            break;
            case('select'):
            inputelement = (<select onChange={props.changed} className={classes.Inputelement}>
                {props.elementconfig.options.map(c=>{
                return(<option key={c.value} value={c.value}>{c.displayValue}</option>)
                })}
            </select>)
            break;
            default:
            inputelement =  <input onChange={props.changed} className={classes.Inputelement} {...props.elementconfig}/>
            break;
        }
    return(
        <div className={classes.Input}>
            {inputelement}
        </div>
    )
}
export default Input


