import React, { Component } from 'react';
import Aux from '../../hoc/Aux/Aux';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary';
import Spinner from '../../components/UI/Spinner/Spinner';
import axios from '../../axios-orders';
import WithErrorHandler from '../../hoc/WithErrorHandler/WithErrorHandler'
import {connect} from 'react-redux';
import * as actionTypes from '../../store/action'

class BurgerBuilder extends Component {
    // constructor(props) {
    //     super(props);
    //     this.state = {...}
    // }
    state = {
        purchasing: false,
        loading: false,
        error:false
    }
 //   componentDidMount(){
 //       axios.get('/ingredients.json')
 //            .then(res=>this.setState({ingredients:res.data}))
 //            .catch(error=>this.setState({error:true}))
            
 //   }
 
        updatePurchaseState = ( ingredients )=> {
            const sum = Object.keys( ingredients )
                .map( igKey => {
                    return ingredients[igKey];
                } )
                .reduce( ( sum, el ) => {
                    return sum + el;
                }, 0 );
            if(sum>0)
            {return true}
            else
            {return false}
        }

    purchaseHandler = () => {
        this.setState( { purchasing: true } );
    }

    purchaseCancelHandler = () => {
        this.setState( { purchasing: false } );
    }

    purchaseContinueHandler = () => {
        // alert('You continue!');
    //    const query = [];
    //    for(let i in this.props.ings)
    //    {
    //        query.push(encodeURIComponent(i)+'='+encodeURIComponent(this.props.ings[i]))
    //    }
    //    query.push('price=' + this.state.totalPrice)
    //    let querystring = query.join('&')
        this.props.history.push({pathname:'/Checkout'}       
    //        search:'?'+querystring}
            )
       /* this.setState( { loading: true } );
        const order = {
            ingredients: this.state.ingredients,
            price: this.state.totalPrice,
            customer: {
                name: 'Max Schwarzmller',
                address: {
                    street: 'Teststreet 1',
                    zipCode: '41351',
                    country: 'Germany'
                },
                email: 'test@test.com'
            },
            deliveryMethod: 'fastest'
        }
        axios.post( '/orders.json', order )
            .then( response => {
                this.setState({ loading: false, purchasing: false });
            } )
            .catch( error => {
               this.setState({ loading: false, purchasing: false });
            } );
            console.log(order)*/
    }

    render () {
       
        const disabledInfo = {
            ...this.props.ings
        };
        for ( let key in disabledInfo ) {
            disabledInfo[key] = disabledInfo[key] <= 0
        }
        
        // {salad: true, meat: false, ...}
        
        let burger= <Spinner/>
        if (this.state.error)
        {burger=<p>Ingredients can't be loaded...</p>}
        if (this.props.ings)
        {       
            let orderSummary = 
                 <OrderSummary
                 ingredients={this.props.ings}
                 price={this.props.totalPrice}
                 purchaseCancelled={this.purchaseCancelHandler}
                 purchaseContinued={this.purchaseContinueHandler} />;
                   if ( this.state.loading ) {
                 orderSummary = <Spinner />;
            } 
            burger = (
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelHandler}>
                    {orderSummary}
                </Modal>
                <Burger ingredients={this.props.ings} />
                <BuildControls
                    ingredientAdded={this.props.onAdd}
                    ingredientRemoved={this.props.onRemove}
                    disabled={disabledInfo}
                    purchasable={this.updatePurchaseState(this.props.ings)}
                    ordered={this.purchaseHandler}
                    price={this.props.totalPrice} />
            </Aux>)
        }
        return (
            burger
        );
    }
}
const mapStateToProps = state =>{
      return{ings:state.ingredients,
             totalPrice:state.totalPrice
    }  
}
const mapDispatchToProps = dispatch =>{
      return{
        onAdd:(ingredientName)=>dispatch({
            type:actionTypes.ADD_INGREDIENT 
            ,ingredientName:ingredientName}),

        onRemove:(ingredientName)=>dispatch({
            type:actionTypes.REMOVE_INGREDIENT 
            ,ingredientName:ingredientName})
      }
}


export default connect(mapStateToProps,mapDispatchToProps)(WithErrorHandler(BurgerBuilder,axios));


