import React,{Component} from 'react'
import Modal from '../../components/UI/Modal/Modal'
import Aux from '../Aux/Aux'


const WithErrorHandler =(WrappedComponent , axios)=>{
return  class extends Component{
    constructor(props){
        super(props)
        this.resInterceptors = axios.interceptors.response.use(res=>res,
            error=>{this.setState({error:error})})
        this.reqInterceptors = axios.interceptors.request.use(
            req=>{this.setState({error:null});return req}
        )
    }
    state={
        error:null
    }
 //   componentWillUnmount(){
 //      axios.interceptors.response.reject(this.resInterceptors)
 //      axios.interceptors.request.reject(this.reqInterceptors)
 //   }
    errorConfirmedHandler=()=>{
        this.setState({error:null})
    }

    render(){
        return(
            <Aux>
            <Modal 
            modalClosed={this.errorConfirmedHandler}    //functionbackdropassignonclick
            show={this.state.error}>{this.state.error? this.state.error.message : null}</Modal>
            <WrappedComponent {...this.props}/>
            </Aux>
             )
        }
    }
}
export default WithErrorHandler



